import React from 'react'
import {useStaticQuery,graphql} from 'gatsby'
import Pagination from '../../../Global/Pages/Pagination.component'
import BreadcrumContent from '../../../Global/Pages/BreadcrumbContent.component'
import { ReactSelectListing } from './ReactSelectShop.component'
import { Featured } from '../../../Global/App/ReactSelect/SelectData'
const SpSearchContent= ()=>{
    return(
        <div className="sp_search_content">
     
            <div className="search_result">
                <p>9 Search results</p>
            </div>
            <div className="search_drpdwn text-right tac-xsd mb25">
                <div className="right_area style2 text-right grid-select-container">
                <ul >
                    <li className="list-inline-item">
                    <span className="shrtby">Sort by:&nbsp;</span>
                    <ReactSelectListing data={Featured}/>
                    </li>
                </ul>
			</div>
            
        </div>
        
        {/* <div className="sp_search_content">
						<div className="search_result">
							<p>9 Search results</p>
						</div>
						<div className="search_drpdwn text-right tac-xsd mb25">
							<div className="dropdown bootstrap-select show-tick"><select className="selectpicker show-tick" tabindex="-98">
								<option>Sort by:  Featured First</option>
								<option>Recent</option>
								<option>Old Review</option>
							</select><button type="button" className="btn dropdown-toggle btn-light" data-toggle="dropdown" role="button" title="Sort by:  Featured First"><div className="filter-option"><div className="filter-option-inner"><div className="filter-option-inner-inner">Sort by:  Featured First</div></div> </div></button><div className="dropdown-menu " role="combobox"><div className="inner show" role="listbox" aria-expanded="false" tabindex="-1"><ul className="dropdown-menu inner show"></ul></div></div></div>
						</div>
					</div> */}
                    </div>
    
    )
}
const ProductBox =({ title,price, children}) =>{
    return(
        <div className="col-sm-6 col-lg-4 col-xl-4">
					<div className="shop_grid">
						<div className="thumb">
                        {children}
							<div className="tag">Sale</div>
						</div>
						<div className="details">
							<h4 className="item-tile">{title} <span className="price">${price}</span></h4>
							<button className="btn btn-thm add_to_cart">Add to cart</button>
						</div>
					</div>
		</div>
    )
 }
const ShopSection=()=>{
    const data = useStaticQuery(graphql`
        query{
            shop1: file(relativePath: {eq: "shop/1.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop2: file(relativePath: {eq: "shop/2.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop3: file(relativePath: {eq: "shop/3.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop4: file(relativePath: {eq: "shop/4.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop5: file(relativePath: {eq: "shop/5.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop6: file(relativePath: {eq: "shop/6.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop7: file(relativePath: {eq: "shop/7.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop8: file(relativePath: {eq: "shop/8.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            shop9: file(relativePath: {eq: "shop/9.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)

    return(
        <section className="our-shop bgc-f7" >
            <div className="container">
                <div className="row">
                    <div className="col-xl-6">
                    <BreadcrumContent title='Shop List' active="Shop List"/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-12">

                        <SpSearchContent/>
                    </div>
                </div>
                <div className="row">
                    <ProductBox title="Album" price="13,000">
                            <img className="img-fluid w100" src={data.shop1.childImageSharp.fluid.originalImg} alt="1.png"/>
                    </ProductBox>
                    <ProductBox title="Beanie" price="13,000">
                            <img className="img-fluid w100" src={data.shop2.childImageSharp.fluid.originalImg} alt="2.png"/>
                    </ProductBox>
                    
                    <ProductBox title="Beanie with Log" price="13,000">
                            <img className="img-fluid w100" src={data.shop3.childImageSharp.fluid.originalImg} alt="3.png"/>
                    </ProductBox>
                   <ProductBox title="Belt" price="13,000">
                            <img className="img-fluid w100" src={data.shop4.childImageSharp.fluid.originalImg} alt="4.png"/>
                    </ProductBox>
                    <ProductBox title="Cap" price="13,000">
                            <img className="img-fluid w100" src={data.shop5.childImageSharp.fluid.originalImg} alt="5.png"/>
                    </ProductBox>
                    <ProductBox title="Hoodie"  price="13,000">
                            <img className="img-fluid w100" src={data.shop6.childImageSharp.fluid.originalImg} alt="6.png"/>
                    </ProductBox>
                    <ProductBox title="Hoodie with Zipper"  price="13,000">
                            <img className="img-fluid w100" src={data.shop7.childImageSharp.fluid.originalImg} alt="7.png"/>
                    </ProductBox>
                    <ProductBox title="Long Sleeve Tee"  price="13,000">
                            <img className="img-fluid w100" src={data.shop8.childImageSharp.fluid.originalImg} alt="8.png"/>
                    </ProductBox>
                    <ProductBox title="Hoodie with Logo"  price="13,000">
                            <img className="img-fluid w100" src={data.shop9.childImageSharp.fluid.originalImg} alt="9.png"/>
                    </ProductBox>
                    <div className="col-lg-12">
                        <Pagination/>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default ShopSection